import React from 'react'
import { WithMarvelService } from '../hoc'
import { useEffect } from 'react';
import { charactersLoaded, loadded } from '../../actions'
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Spiner from '../spiner';
import Paggination from '../paggination'


const MainComicsPage = (props) => {
    
    const { marvelApi, charactersLoaded, characters, page, loading, loadded, onSelectCharacterd, history } = props;
    useEffect(()=>{
        marvelApi.getAllComics(page).then((data)=>{charactersLoaded(data)})},[ page, charactersLoaded, loadded, marvelApi ]
    )
    if (loading) {
        return <Spiner />
    }
    else{
        console.log(Array.isArray(props.characters[4].images))
    return (
        <div className='d-flex flex-wrap container'>
            <div className='row flex-wrap'>
            {
                characters.map((item)=>{
                    return <div key={item.id} className='col-xl-4 col-sm-6 text-center mt-2' onClick={(id) => history.push(`/comics/${item.id}`)}>
                            <p className='comics__right'>{item.title}</p>
                            <img src={Array.isArray(item.images)? item.images[0] : item.images} alt=""/>
                            <p className='comics__left'>{item.description}</p>
                        </div>
                })
            }
            </div>
            <Paggination />
        </div>
    )}
}



const mapStateToProps = ({ characters, page, loading, error}) => {
    return { characters, page, loading, };
};


//ownProps
const mapDispatchToProps = (dispatch) => {
    return {
        charactersLoaded: bindActionCreators(charactersLoaded, dispatch),
        loadded: bindActionCreators(loadded, dispatch),
        };
    };

export default WithMarvelService(
    connect(mapStateToProps, mapDispatchToProps)(MainComicsPage))